.project-heading{
    text-align: center;
    padding: 4rem 0 2rem 0;
    
}

.project-img{
    width: 100%;
    height: 200px;
}

.project-container{
    max-width: 1140px;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(3,1fr);
    grid-gap: 40px;
    padding-bottom: 5rem;

}

.project-card{
    background: #1a1919;
    padding: 1.2rem 1rem;

}



.project-card img{
    width: 100%;
}


.project-title{
    color:#fff;
    padding: 1rem 0;

}

.pro-details p{
    padding-bottom: 1rem;
    font-size: 1rem;
    text-align: justify;
}


.pro-btns{
    display: flex;
    justify-content: space-between;
    padding: 0.5rem 0;
  
}

.pro-btns .btn{
    position: static;
    height: 2.5rem;
    width: 20rem;
    padding: 0.5rem 1rem;
    text-align: center;
    margin: 0;
}

@media screen and (max-width: 640px){
    
    .project-container{
        max-width: 90%;
        margin: auto;
        grid-template-columns: repeat(1,1fr);
    }
}
